.icon {
  background-position: center;
  background-size: cover;
  width: 18px;
  height: 18px;
  transition: transform .3s;
  display: block;
}

.icon:hover {
  transform: scale(1.2);
}

@media (width >= 320px) {
  .icon--menu {
    width: 18px;
    height: 18px;
  }
}

@media (width >= 576px) {
  .icon {
    width: 32px;
    height: 32px;
  }
}

.icon--close {
  background-image: url("close.18e33f94.png");
}

.icon--phone {
  background-image: url("Vector.f2f5b026.png");
}

.icon--menu {
  background-image: url("Vector (1).d9591b64.png");
}

.icon--menu:hover {
  background-image: url("Icon-Burger-menu-hover.6d84898d.png");
}

@media (width >= 1200px) {
  .phone-icon {
    display: inline-block;
    position: relative;
  }

  .phone-icon:before {
    content: attr(data-phone-number);
    letter-spacing: 2px;
    color: #fff;
    opacity: 0;
    pointer-events: none;
    white-space: nowrap;
    z-index: 1;
    margin-right: 20px;
    padding: 10px;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    position: absolute;
    top: 0;
    right: 100%;
  }

  .phone-icon:hover:before {
    opacity: 1;
  }
}
