@import 'variables';

.icon {
  display: block;
  width: 18px;
  height: 18px;
  background-size: cover;
  background-position: center;
  transition: transform 0.3s;

  &:hover {
    transform: scale(1.2);
  }

  @media (min-width: 320px) {
    &--menu {
      width: 18px;
      height: 18px;
    }
  }

  @media (min-width: 576px) {
    height: 32px;
    width: 32px;
  }

  &--close {
    background-image: url(/src/icons/close.png);
  }

  &--phone {
    background-image: url(/src/icons/Vector.png);
  }

  &--menu {
    background-image: url(/src/icons/Vector\ \(1\).png);

    &:hover {
      background-image: url(/src/icons/Icon-Burger-menu-hover.png);
    }
  }
}

@media (min-width: 1200px) {
  .phone-icon {
    position: relative;
    display: inline-block;
  }

  .phone-icon::before {
    content: attr(data-phone-number);
    font-weight: 700;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    position: absolute;
    top: 0;
    right: 100%; /* Зсувати від правого краю значка */
    margin-right: 20px; /* Відстань між значком і текстом */
    color: $colorWhite;
    padding: 10px 10px; /* Подальші налаштування оформлення, такі як кольори та відступи */
    opacity: 0;
    pointer-events: none; /* Вимкнення обробки подій для тексту */
    white-space: nowrap; /* Забезпечення того, щоб текст не переносився на новий рядок */
    z-index: 1; /* Забезпечення правильного порядку відображення над іконкою */
  }

  .phone-icon:hover::before {
    opacity: 1;
  }
}
